@import url(https://fonts.googleapis.com/css2?family=Aldrich&family=Comfortaa:wght@300;700&family=Dancing+Script&family=Roboto:wght@100&display=swap);
.App {
  /* text-align: center; */
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


body {
  /* background-image: url("./images/Background-site4.jpg"); */
  cursor: url(/Portfolio-React/static/media/customArrow.e9b2593f.svg), auto;
  background-color: #3d6376;
  background-attachment: fixed;
}



  
ul {
  list-style-type: none;
  display: flex;
  justify-self: flex-start;
  padding: 30px;
}

li {
  font-family: "Aldrich";
  font-size: 30px;
  padding: 10px;
  margin-top: -10px;
}

li a:hover {
  cursor: url(/Portfolio-React/static/media/darkCustomArrow.bb43ac29.svg), auto;
  color: #28738c;
}

li a {
  text-decoration: none;
  color: #6eb8c7;
}

.socials {
  margin-top: -10px;
  font-size: 45px;
  padding: 5px;
  text-decoration: none;
  color: #6eb8c7;
  cursor: url(/Portfolio-React/static/media/darkCustomArrow.bb43ac29.svg), auto;
}

.socials:hover {
  color: white;
}

@media screen and (max-width: 600px) {
  .navbar{
    display: flex;
    margin-left: -15px;
  }
  li {
   display: flex;
   font-size: 14px;
   margin-left: -10px;
  
  }
  .socials {
    display: flex;
    font-size: 22px;

  }
  .default .defaultLogo{
    margin-left: -30px;
  }
}

.infoCard {
  font-family: "Aldrich";
  color: white;
  font-size: 18px;
  line-height: 50px;
  font-weight: 10;
  max-width: 670px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10px;
}

.infoCard .hi {
  font-size: 40px;
  -webkit-animation-name: fading;
          animation-name: fading;
  -webkit-animation-duration: 5000ms;
          animation-duration: 5000ms;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}

.infoCard .look {
  font-size: 33px;
  -webkit-animation-name: fading2;
          animation-name: fading2;
  -webkit-animation-duration: 6000ms;
          animation-duration: 6000ms;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}

.Face {
  width: 230px;
  height: 230px;
}

@-webkit-keyframes fading {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fading {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes fading2 {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fading2 {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}


@media screen and (max-width: 600px) {
  .infoCard{
    margin-top: 10px;
  }
}

a.infoCard.Face{
  scroll-margin-top: 20px;
}
.Card {
  margin-top: 200px;
  display: flex;
  justify-content: center;
  /* gap: 100px; */
}

.Card img {
  border: 10px solid rgba(255, 255, 255, 0.06);
  width: 100 vw;
  height: 400px;
  opacity: 0.5;
  cursor: url(/Portfolio-React/static/media/darkCustomArrow.bb43ac29.svg), auto;
  object-fit: cover;
}

.Card img:hover {
  border: 10px solid #bfe7f1;
  opacity: 1;
  border-radius: 5px;
}

.breakfast {
  color: white;
  font-family: "Aldrich";
  font-size: 22px; 
  text-align: center;
}

.typingGame {
  color: white;
  font-family: "Aldrich";
  font-size: 22px;
  text-align: center;
}

.scraper {
  color: white;
  font-family: "Aldrich";
  font-size: 22px;
  text-align: center;
}

@media screen and (max-width: 600px) {
  .Card {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .Card img {
    width: 330px;
    height: 330px;
    /* object-fit: cover; */
  }
}

.defaultLogo {
  display: block;
  padding-left: 39px;
  padding-top: 66px;
}

.defaultLogo:hover {
  -webkit-animation: spin 1s linear;
          animation: spin 1s linear;
  padding-left: 39px;
  padding-right: 39px;
}
@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotateY(360deg);
  }
}
@keyframes spin {
  100% {
    -webkit-transform: rotateY(360deg);
    transform: rotateY(360deg);
  }
}




